import {mobileVhFix} from './utils/mobile-vh-fix.js';
import {Form} from './modules/form-validate/form';
import {initMenu} from './modules/menu/init-menu';
import {initScrollToFooter} from './modules/scroll/scroll-to-footer';
import {initScrollMenu} from './modules/scroll/scroll-menu';
import {initAccordions} from './modules/accordions/init-accordion';
import {initVideos} from './modules/videos/init-videos';
import {initAnimation} from './modules/animation/init-animation';


// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  // Utils
  // ---------------------------------

  mobileVhFix();
  initMenu();
  initScrollToFooter();
  initScrollMenu();

  // Modules
  // ---------------------------------

  //       'DOMContentLoaded',     'load'
  //  load   ,      
  window.addEventListener('load', () => {
    const form = new Form();
    window.form = form;
    form.init();
    initAccordions();
    initVideos();
    initAnimation();
  });
});

